.art_button {

    width: 100vw;
    padding: 30px;

    display: flex;
    justify-content: space-around;
    column-gap: 30px;

    text-align: center;

    button {
        background-color: rgb(108, 106, 106);
        padding: 5px 10px;
        border-radius: 10px;
        color: white;
        font-weight: bold;

        &:hover {
            color: yellowgreen;
            box-shadow: 5px 5px 5px 1px yellowgreen;
        }

        &:active {
            background-color: rgb(54, 226, 106);
            scale: 1.2;
            color: white;
        }

    }
}

#textSuche {
    margin-top: 20px;
    padding: 5px 40px;
    border-radius: 20px;
	border: 10px linear-gradient(21deg, #10abff, #1beabd);
    pointer-events: visibleFill;

    box-shadow: inset 
    3px -3px 30px #1beabd, 
    -3px 3px 30px #10abff;
}